(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          // $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, $firebaseArray, $log, $sanitize) {
    var vm = this;

    vm.sayings = [];
    vm.addSaying = addSaying;
    vm.randomSaying = '';
    var ref = new Firebase('https://gerbsdawg.firebaseio.com/sayings');

    activate();

    function activate() {

      vm.sayings = $firebaseArray(ref);
      $log.log(vm.sayings);

      // seedData();
    }

    function seedData() {
      vm.addSaying('Ask questions', 'Answers questions you haven\'t even thought to ask');
    }

    function addSaying(gerbMessage, dawgMessage) {
      vm.sayings.$add({
        gerbMessage: gerbMessage,
        dawgMessage: dawgMessage
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .controller('AddController', AddController);

  /** @ngInject */
  function AddController($firebaseArray, $window, toastr) {
    var vm = this;

    vm.sayings = [];
    vm.addSaying = addSaying;
    vm.removeSaying = removeSaying;
    vm.newSaying = {};
    var ref = new Firebase('https://gerbsdawg.firebaseio.com/sayings');

    activate();

    function activate() {
      vm.sayings = $firebaseArray(ref);
    }

    function addSaying() {
      vm.sayings.$add(vm.newSaying);
      vm.newSaying = {};
      toastr.success('New saying added.');
    }

    function removeSaying(saying) {
      if($window.confirm('Are you sure?')) {
        vm.sayings.$remove(saying);
        toastr.error('Saying removed.');
      }
    }
  }
})();

